import { Inject, Injectable, InjectionToken, Optional } from '@angular/core';
import { combineLatest, NextObserver } from 'rxjs';
import { ItemModel } from '@backbase/foundation-ang/core';
import { AccountAliasDisplayingLevel, ProductKindUri } from '@backbase/product-summary-common-ang';
import { AvailableFilterEnum, AvailableFiltersConfig, EndpointType } from '@backbase/transactions-common-ang';
import { CreditLimitViewMode } from '@backbase/product-summary-ui';
import { AccountInfoPropertiesConfiguration } from './views/account-info/model/types';
import { defaultAccountInfoConfiguration } from './views/account-info/model/configuration';

export interface AccountsTransactionsJourneyConfiguration {
  /**
   * API key for Google Maps
   */
  apiKey: string;
  /**
   * Number of items loaded initialy and with each load-more request
   */
  itemsPerPage: number;
  /**
   * Maximum number of pagination pages in transactions
   */
  transactionsMaxPages: number;
  /**
   * Pagination type for accounts and transactions
   */
  paginationType: 'load-more' | 'pagination' | 'infinite-scroll';
  /**
   * Pending transactions on top and hides billing status filter
   */
  pendingOnTop: boolean;
  /**
   * Options to initialize the filter (format as query parameters)
   */
  transactionsFilterParameters: string;
  /**
   * Options to initialize the sorting (e.g. 'amount ASC' or empty)
   */
  transactionsSortOptions: string;
  /**
   * Show transactions control such as filter and search
   */
  showControls: boolean;
  /**
   * Enable change category in transactions details
   */
  showChangeCategory: boolean;
  /**
   * Display check images in transaction details
   */
  showCheckImages: boolean;
  /**
   * Making GET or POST requests for transactions
   */
  getOrPostEndpoints: EndpointType;
  /**
   * Enable dispute and inquiry report in transactions list
   */
  enableDisputeAndInquiry: boolean;
  /**
   * The transaction is eligible for a dispute based on the eligibility period
   */
  disputeEligibilityDays: number;
  /**
   * Disputable transaction types
   */
  disputeTransactionTypes: string;
  /**
   * The billing status of the transaction is disputable
   */
  disputeByBillingStatus: boolean;
  /**
   * Inquire transaction topic ID
   */
  inquireTopicId: string;
  /**
   * Dispute transaction topic ID
   */
  disputeTopicId: string;
  /**
   * Configure available filters based on product kind product type
   */
  availableFiltersConfig: AvailableFiltersConfig;
  /**
   * Hide/show account type icons
   */
  showAccountIcons: boolean;
  /**
   * Enable/disable manage accounts
   */
  enableManageAccounts: boolean;
  /**
   * Enable `make a transfer` button in the account details tab view
   */
  enableTransferButton: boolean;
  /**
   * Product kinds for which graphical representation should be enabled in account details
   */
  productKindsWithGraphicalRepresentation: ProductKindUri | ProductKindUri[];
  /**
   * Product kinds for which account details should be displayed on an external page
   */
  productKindsWithExternalDetailsPage: string | string[];
  /**
   * Credit limit view mode, 'emptied' or 'filled'
   */
  creditLimitViewMode: CreditLimitViewMode;
  /**
   * Account alias displaying level for accounts
   */
  accountAliasDisplayLevel: AccountAliasDisplayingLevel;
  /**
   * Account info properties configuration
   */
  accountInfoProperties: AccountInfoPropertiesConfiguration;
  /**
   * Arrangements view name
   */
  arrangementViewsName?: string;
}

// eslint-disable-next-line
export const AccountsTransactionsJourneyConfigurationToken =
  new InjectionToken<AccountsTransactionsJourneyConfiguration>(
    'AccountsTransactionsJourneyConfiguration injection token',
  );

const configDefaults: AccountsTransactionsJourneyConfiguration = {
  apiKey: '',
  itemsPerPage: 10,
  transactionsMaxPages: 3,
  paginationType: 'load-more',
  pendingOnTop: false,
  transactionsFilterParameters: '',
  transactionsSortOptions: 'bookingDate DESC',
  showControls: true,
  showChangeCategory: true,
  showCheckImages: false,
  getOrPostEndpoints: EndpointType.GET_REQUEST,
  enableDisputeAndInquiry: true,
  disputeEligibilityDays: 120,
  disputeTransactionTypes: 'Credit/Debit Card',
  disputeByBillingStatus: true,
  disputeTopicId: '',
  inquireTopicId: '',
  availableFiltersConfig: {
    'Current Account': [
      AvailableFilterEnum.Date,
      AvailableFilterEnum.CreditDebitIndicator,
      AvailableFilterEnum.Amount,
      AvailableFilterEnum.Types,
      AvailableFilterEnum.BillingStatus,
      AvailableFilterEnum.CheckSerialNumber,
    ],
    'Savings Account': [
      AvailableFilterEnum.Date,
      AvailableFilterEnum.CreditDebitIndicator,
      AvailableFilterEnum.Amount,
      AvailableFilterEnum.Types,
      AvailableFilterEnum.BillingStatus,
      AvailableFilterEnum.CheckSerialNumber,
    ],
    'Credit Card': [
      AvailableFilterEnum.Date,
      AvailableFilterEnum.CreditDebitIndicator,
      AvailableFilterEnum.Amount,
      AvailableFilterEnum.Types,
      AvailableFilterEnum.BillingStatus,
    ],
    Loan: [AvailableFilterEnum.Date, AvailableFilterEnum.CreditDebitIndicator, AvailableFilterEnum.Amount],
    'Term Deposit': [AvailableFilterEnum.Date, AvailableFilterEnum.CreditDebitIndicator, AvailableFilterEnum.Amount],
    'Debit Card': [AvailableFilterEnum.Date, AvailableFilterEnum.CreditDebitIndicator, AvailableFilterEnum.Amount],
    'Investment Account': [
      AvailableFilterEnum.Date,
      AvailableFilterEnum.CreditDebitIndicator,
      AvailableFilterEnum.Amount,
    ],
  },
  showAccountIcons: true,
  enableManageAccounts: true,
  enableTransferButton: true,
  productKindsWithGraphicalRepresentation: ProductKindUri.CREDIT_CARD,
  productKindsWithExternalDetailsPage: [],
  creditLimitViewMode: 'emptied',
  accountAliasDisplayLevel: AccountAliasDisplayingLevel.USER,
  accountInfoProperties: defaultAccountInfoConfiguration,
  arrangementViewsName: undefined,
};

@Injectable()
export class AccountsTransactionsJourneyService {
  private _config: AccountsTransactionsJourneyConfiguration;

  constructor(
    @Optional()
    @Inject(AccountsTransactionsJourneyConfigurationToken)
    config: Partial<AccountsTransactionsJourneyConfiguration>,
    @Optional() private readonly itemModel: ItemModel,
  ) {
    config = config || {};
    this._config = { ...configDefaults, ...config };
    // update the API key from ItemModel if it was not provided from a static config
    if (this.itemModel && (config.apiKey === undefined || config.showCheckImages === undefined)) {
      combineLatest([
        this.itemModel.property<string>('apiKey', this._config.apiKey),
        this.itemModel.property<boolean>('showCheckImages', this._config.showCheckImages),
      ])
        .subscribe({
          next: (values: [string, boolean]) => {
            if (config.apiKey === undefined) {
              this._config.apiKey = values[0];
            }
            if (config.showCheckImages === undefined) {
              this._config.showCheckImages = !!values[1];
            }
          },
        } as NextObserver<[string, boolean]>)
        .unsubscribe();
    }
  }

  get defaults(): AccountsTransactionsJourneyConfiguration {
    return configDefaults;
  }

  get apiKey(): string {
    return this._config.apiKey;
  }

  get itemsPerPage(): number {
    return this._config.itemsPerPage;
  }

  get transactionsMaxPages(): number {
    return this._config.transactionsMaxPages;
  }

  get paginationType(): string {
    return this._config.paginationType;
  }

  get pendingOnTop(): boolean {
    return this._config.pendingOnTop;
  }

  get transactionsFilterParameters(): string {
    return this._config.transactionsFilterParameters;
  }

  get transactionsSortOptions(): string {
    return this._config.transactionsSortOptions;
  }

  get showControls(): boolean {
    return this._config.showControls;
  }

  get showChangeCategory(): boolean {
    return this._config.showChangeCategory;
  }

  get showCheckImages(): boolean {
    return this._config.showCheckImages;
  }

  get getOrPostEndpoints(): EndpointType {
    return this._config.getOrPostEndpoints;
  }

  get enableDisputeAndInquiry(): boolean {
    return this._config.enableDisputeAndInquiry;
  }

  get disputeEligibilityDays(): number {
    return this._config.disputeEligibilityDays;
  }

  get disputeTransactionTypes(): string {
    return this._config.disputeTransactionTypes;
  }

  get disputeByBillingStatus(): boolean {
    return this._config.disputeByBillingStatus;
  }

  get disputeTopicId(): string {
    return this._config.disputeTopicId;
  }

  get inquireTopicId(): string {
    return this._config.inquireTopicId;
  }

  get availableFiltersConfig(): AvailableFiltersConfig {
    return this._config.availableFiltersConfig;
  }

  get enableManageAccounts(): boolean {
    return this._config.enableManageAccounts;
  }

  get showAccountIcons(): boolean {
    return this._config.showAccountIcons;
  }

  get enableTransferButton(): boolean {
    return this._config.enableTransferButton;
  }

  get productKindsWithGraphicalRepresentation(): ProductKindUri | ProductKindUri[] {
    return this._config.productKindsWithGraphicalRepresentation;
  }

  get productKindsWithExternalDetailsPage(): string | string[] {
    return this._config.productKindsWithExternalDetailsPage;
  }

  get creditLimitViewMode(): CreditLimitViewMode {
    return this._config.creditLimitViewMode;
  }

  get accountAliasDisplayLevel(): AccountAliasDisplayingLevel {
    return this._config.accountAliasDisplayLevel;
  }

  /**
   * Account info properties configuration
   */
  get accountInfoProperties(): AccountInfoPropertiesConfiguration {
    return this._config.accountInfoProperties;
  }

  get arrangementViewsName(): string | undefined {
    return this._config.arrangementViewsName;
  }
}
